
import BirdyService from '../BirdyService'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

import Upload from './Upload'

export default {
  components: {
    Alert, Toast, Swiper, XButton, Box, Upload
  },
  data () {
    return {
      birdyService: new BirdyService(this),
      position: 'default',
      showTip: false,
      show: false,
      result: '贷款申请提交结果',
      errorTip: '请完善表单内容',
      content: {},
      target: {
        loan_amount: 0,
        income: 0,
        id_card_front: '',
        bank_pic: ''
      },
      demo06_index: 0,
      demo06_list: []
    }
  },
  created () {
    this.pageInit()
  },
  methods: {
    demo06_onIndexChange (index) {
      this.demo06_index = index
    },
    pageInit () {
      this.birdyService.get(`/api/content/1?access-token=abc123_`).then((resp) => {
        this.content = resp.content
        this.demo06_list = resp.banners.map((item, index) => ({
          url: '',
          img: item.url,
          title: ''
        }))
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    },
    apply () {
      if (!this.target.name) {
        this.showTip = true
        this.errorTip = "请填写您的真实姓名"
        return false
      }
      if (!this.target.tel) {
        this.showTip = true
        this.errorTip = "请填写您的手机号码"
        return false
      } else if (!(/^1[34578]\d{9}$/.test(this.target.tel))) {
        this.showTip = true
        this.errorTip = "请输入正确手机号码"
        return false
      }
      if (this.target.loan_amount == 0) {
        this.showTip = true
        this.errorTip = "请选择贷款金额"
        return false
      }
      if (!this.target.occupation) {
        this.showTip = true
        this.errorTip = "请填写您的职业"
        return false
      }
      if (this.target.income == 0) {
        this.showTip = true
        this.errorTip = "请选择您的家庭年收入"
        return false
      }
      if (!this.target.id_card_front) {
        this.showTip = true
        this.errorTip = "请上传身份证正面照片"
        return false
      }
      if (!this.target.bank_pic) {
        this.showTip = true
        this.errorTip = "请上传银行卡照片"
        return false
      }

      this.birdyService.create(`/api/loan/create?access-token=abc123_`).then((resp) => {
        // this.$router.push({ path: `/confirm/${resp.id}` })
        this.show = true
        this.target = {
          loan_amount: 0,
          income: 0,
          id_card_front: '',
          bank_pic: ''
        }
      }).catch((resp) => {
        console.log(resp)
      }).finally(() => {

      })
    }
  }
}



